<template>
  <div class="crypto">
    <div class="create-post">
      <label for="create-post">Say Something...</label>
      <input type="text" id="create-post" v-model="text" placeholder="Create a post">
      <button @click="createTweet">POSTPOST!</button>
    </div>
    <div class="tweet-container">
      <h2>Tweets You love</h2>
      <p class="error" v-if="error">{{ error }}</p>
      <div class="tweets"
        v-for="(tweet, index) in tweets"
        v-bind:item="tweet"
        v-bind:index="index"
        v-bind:key="tweet._id"
        @dblclick="deleteTweet(tweet._id)"
      >
        {{ `${tweet.createdAt.getDate()}/${tweet.createdAt.getMonth()}/${tweet.createdAt.getFullYear()}` }}
        <p class="text">{{ tweet.text }}</p>
        <p>{{ index }}</p>
      </div>
    </div>
  </div>
</template>

<script>

import TwitterService from '../TwitterService.js';

export default {
  name: 'TweetComponent',
  components: {
  },
  data() {
    return {
      tweets: [],
      error: '',
      text: ''
    }
  },
  async created(err) {
    try {
      this.tweets = await TwitterService.getTweets();
    } catch {
      this.error = err.message;
    }
  },
  methods: {
    async createTweet() {
      await TwitterService.insertTweet(this.text);
      this.tweets = await TwitterService.getTweets();
    },
    async deleteTweet(id) {
      await TwitterService.deleteTweet(id);
      this.tweets = await TwitterService.getTweets();
    }
  }
}
</script>
